import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import Login from "./views/Login.vue";
import Register from "./views/Register.vue";

Vue.use(Router);

// export default new Router({
export const router = new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      // name: 'home',
      component: Home,
    },
    {
      path: "/home",
      component: Home,
    },
    {
      path: "/login",
      component: Login,
    },
    {
      path: "/register",
      component: Register,
    },
    {
      path: "/profile",
      name: "profile",
      // lazy-loaded
      component: () => import("./views/Profile.vue"),
    },
    {
      path: "/bi",
      name: "bodily-injury",
      // lazy-loaded
      component: () => import("./views/Department/BI.vue"),
    },
    {
      path: "/od",
      name: "own-damage",
      // lazy-loaded
      component: () => import("./views/Department/OD.vue"),
    },
    {
      path: "/tpbi",
      name: "third-party-bodily-injury",
      // lazy-loaded
      component: () => import("./views/Department/TPBI.vue"),
    },
    {
      path: "/admin",
      name: "admin",
      // lazy-loaded
      component: () => import("./views/BoardAdmin.vue"),
    },
    // {
    //   path: '/mod',
    //   name: 'moderator',
    //   // lazy-loaded
    //   component: () => import('./views/BoardModerator.vue'),
    // },
    {
      path: "/manager",
      name: "manager",
      // lazy-loaded
      component: () => import("./views/Manager.vue"),
    },
    {
      path: "/user",
      name: "user",
      // lazy-loaded
      component: () => import("./views/BoardUser.vue"),
    },
    // {
    //   path: '/tutorials',
    //   // alias: '/tutorials',
    //   name: 'tutorials',
    //   component: () => import('./components/tutorials/TutorialsList'),
    // },
    // {
    //   path: '/tutorials/:id',
    //   name: 'tutorial-details',
    //   component: () => import('./components/tutorials/Tutorial'),
    // },
    // {
    //   path: '/add',
    //   name: 'add',
    //   component: () => import('./components/tutorials/AddTutorial'),
    // },

    // MEMBERS ROUTES
    {
      path: "/members",
      // alias: '/members',
      name: "members",
      component: () => import("./components/members/MemberList"),
    },
    {
      path: "/members/:id",
      name: "member-details",
      component: () => import("./components/members/Member"),
    },
    {
      path: "/addMember",
      name: "add-member",
      component: () => import("./components/members/AddMember"),
    },

    // BRANCHES ROUTES

    {
      path: "/branches",
      name: "branches",
      component: () => import("./components/branches/BranchList"),
    },
    {
      path: "/branches/:id",
      name: "branch-details",
      component: () => import("./components/branches/Branch"),
    },
    {
      path: "/addBranch",
      name: "add-branch",
      component: () => import("./components/branches/AddBranch"),
    },

    // DEPARTMENTS ROUTES

    {
      path: "/depts",
      name: "depts",
      component: () => import("./components/depts/DeptList"),
    },
    {
      path: "/depts/:id",
      name: "dept",
      component: () => import("./components/depts/Dept"),
    },
    {
      path: "/addDept",
      name: "add-dept",
      component: () => import("./components/depts/AddDept"),
    },

    // INSURER ROUTES

    {
      path: "/inss",
      name: "inss",
      component: () => import("./components/inss/InsList"),
    },
    {
      path: "/inss/:id",
      name: "insurer",
      component: () => import("./components/inss/Ins"),
    },
    {
      path: "/addIns",
      name: "add-ins",
      component: () => import("./components/inss/AddIns"),
    },

    // STAGE ROUTES

    {
      path: "/stages",
      name: "stages",
      component: () => import("./components/stages/StageList"),
    },
    {
      path: "/stages/:id",
      name: "stage",
      component: () => import("./components/stages/Stage"),
    },
    {
      path: "/addStage",
      name: "add-stage",
      component: () => import("./components/stages/AddStage"),
    },

    // CASEFILE ROUTES

    {
      path: "/casefiles",
      name: "casefiles",
      component: () => import("./components/casefiles/CasefileList"),
    },
    {
      path: "/casefiles/:id",
      name: "casefile",
      component: () => import("./components/casefiles/Casefile"),
    },
    {
      path: "/addCasefile",
      name: "add-casefile",
      component: () => import("./components/casefiles/AddCasefile"),
    },

    // MISC ROUTES
    {
      path: "/soon",
      name: "soon",
      component: () => import("./components/Soon"),
    },
  ],
});
router.beforeEach((to, from, next) => {
  // const publicPages = ['/login', '/register', '/home'];
  const publicPages = ["/login"];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = localStorage.getItem("user");

  // trying to access a restricted page + not logged in
  // redirect to login page
  if (authRequired && !loggedIn) {
    next("/login");
  } else {
    next();
  }
});
