<template>
  <body>
    <v-container>
      <div>
        <v-row class="mt-10">
          <v-col cols="12" lg="6" md="6" sm="12" offset-sm="0">
            <div class="card card-container">
              <!-- <img id="profile-img" src="../assets/aasb-logo.png" /> -->
              <img class="profile-img-card" src="../assets/aasb-logo.png" />
              <form name="form" @submit.prevent="handleLogin">
                <div class="form-group">
                  <label for="email">Email</label>
                  <input
                    v-model="user.email"
                    v-validate="'required'"
                    type="text"
                    class="form-control"
                    name="email"
                  />
                  <div
                    v-if="errors.has('email')"
                    class="alert alert-danger"
                    role="alert"
                  >
                    Email is required!
                  </div>
                </div>
                <div class="form-group">
                  <label for="password">Password</label>
                  <input
                    v-model="user.password"
                    v-validate="'required'"
                    type="password"
                    class="form-control"
                    name="password"
                  />
                  <div
                    v-if="errors.has('password')"
                    class="alert alert-danger"
                    role="alert"
                  >
                    Password is required!
                  </div>
                </div>
                <div class="form-group">
                  <button class="btn btn-primary btn-block" :disabled="loading">
                    <span
                      v-show="loading"
                      class="spinner-border spinner-border-sm"
                    ></span>
                    <span>Login</span>
                  </button>
                </div>
                <div class="form-group">
                  <div v-if="message" class="alert alert-danger" role="alert">
                    {{ message }}
                  </div>
                </div>
              </form>
            </div>
          </v-col>

          <v-col cols="12" lg="6" md="6" sm="12" class="d-none d-sm-flex">
            <div>
              <v-col>
                <v-system-bar window color="#0c4c94" dark>
                  <v-icon>mdi-message</v-icon>
                  <span>Latest News</span>
                  <v-spacer></v-spacer>
                  <v-icon>mdi-minus</v-icon>
                  <v-icon>mdi-checkbox-blank-outline</v-icon>
                  <v-icon>mdi-close</v-icon>
                </v-system-bar>
              </v-col>
              <v-col>
                <v-alert border="left" color="#04934c" dense type="info"
                  >All New Reporting System<br />
                  <small
                    ><p>
                      Lorem, ipsum dolor sit amet consectetur adipisicing elit.
                      Laudantium distinctio, unde odio aliquid ab, illo natus
                      eum perferendis nemo non alias perspiciatis velit
                      asperiores aliquam illum exercitationem neque quos.
                      Adipisci?
                    </p></small
                  ></v-alert
                >
              </v-col>
              <v-col>
                <v-alert border="left" color="#04934c" dense type="info"
                  >Please follow SOP<br />
                  <small
                    ><p>
                      Lorem, ipsum dolor sit amet consectetur adipisicing elit.
                      Laudantium distinctio, unde odio aliquid ab, illo natus
                      eum perferendis nemo non alias perspiciatis velit
                      asperiores aliquam illum exercitationem neque quos.
                      Adipisci?
                    </p></small
                  ></v-alert
                >
              </v-col>
            </div>
          </v-col>
        </v-row>
      </div>
    </v-container>
  </body>
</template>

<script>
import User from "../models/user";

export default {
  name: "Login",
  data() {
    return {
      user: new User("", ""),
      loading: false,
      message: "",
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/profile");
    }
  },
  methods: {
    handleLogin() {
      this.loading = true;
      this.$validator.validateAll().then((isValid) => {
        if (!isValid) {
          this.loading = false;
          return;
        }

        if (this.user.email && this.user.password) {
          this.$store.dispatch("auth/login", this.user).then(
            () => {
              this.$router.push("/home");
            },
            (error) => {
              this.loading = false;
              this.message =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
            }
          );
        }
      });
    },
  },
};
</script>

<style scoped>
body {
  background: url(../assets/login-bg.jpg) no-repeat;
  width: 100%;
  height: 100vh;
}

label {
  display: block;
  margin-top: 10px;
}

.card-container.card {
  max-width: 350px !important;
  padding: 40px 40px;
}

.card {
  background-color: #f7f7f7;
  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  /* margin-top: 50px; */
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.profile-img-card {
  width: 100%;
  height: 96px;
  margin: 0 auto 10px;
  /* display: block; */
  /* -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%; */
}
</style>
